<template>
  <transition-group
    name="fade-move"
    tag="div"
    class="h-[88px] sticky overflow-hidden flex items-center"
  >
    <!--   -->
    <p
      v-if="currentText"
      :key="currentText"
      class="text-move text-green-p300 font-900"
      :style="textPositionStyle"
    >
      {{ currentText }}
    </p>

    <!--   -->
    <p
      v-if="middleText"
      :key="middleText"
      class="text-move next text-green-p300 font-900"
      :style="textPositionStyle"
    >
      {{ middleText }}
    </p>

    <!--   -->
    <div v-if="staticText">
      <span
        :key="'static-span'"
        class="space-nowrap font-700"
        :style="{ paddingLeft: `${staticTextPadding}px` }"
      >
        {{ staticText }}
      </span>
    </div>
  </transition-group>
</template>

<script>
export default {
  props: {
    texts: {
      type: Array,
      required: true,
    },
    staticText: {
      type: String,
      required: false,
      default: "",
    },
    initialDelay: {
      type: Number,
      required: false,
      default: 1000,
    },
    transitionSpeed: {
      type: Number,
      required: false,
      default: 2000,
    },
    middleTextDelay: {
      type: Number,
      required: false,
      default: 2000,
    },
    staticTextPadding: {
      type: Number,
      required: false,
      default: 0,
    },
    textPosition: {
      type: Object,
      required: false,
      default: () => ({ position: "absolute", top: "0px", left: "0px" }),
    },
  },
  data() {
    return {
      currentIndex: 0,
      currentText: this.texts[0],
      middleText: null, //   
      intervalId: null,
    };
  },
  computed: {
    textPositionStyle() {
      return {
        position: this.textPosition.position || "absolute", //  "absolute" 
        top: this.textPosition.top || "0px",
        left: this.textPosition.left || "0px",
      };
    },
  },
  methods: {
    rotateText(nextIndex = null) {
      const targetIndex =
        nextIndex !== null
          ? nextIndex
          : (this.currentIndex + 1) % this.texts.length;

      //   
      this.middleText = this.texts[targetIndex];

      //   
      setTimeout(() => {
        this.currentText = this.middleText;
        this.middleText = null; //   
        this.currentIndex = targetIndex;

        //     
        this.$emit("update", targetIndex);
      }, this.transitionSpeed);
    },
    startAutoRotate() {
      //   : initialDelay  
      setTimeout(() => {
        this.rotateText();

        //  : transitionSpeed  
        this.intervalId = setInterval(() => {
          this.rotateText();
        }, this.transitionSpeed + this.middleTextDelay); //   
      }, this.initialDelay);
    },
    stopAutoRotate() {
      if (this.intervalId) {
        clearInterval(this.intervalId);
        this.intervalId = null;
      }
    },
    next() {
      this.stopAutoRotate(); //   
      const nextIndex = (this.currentIndex + 1) % this.texts.length;
      this.rotateText(nextIndex);
      this.startAutoRotate(); //   
    },
    prev() {
      this.stopAutoRotate(); //   
      const prevIndex =
        (this.currentIndex - 1 + this.texts.length) % this.texts.length;
      this.rotateText(prevIndex);
      this.startAutoRotate(); //   
    },
  },
  mounted() {
    this.startAutoRotate();
  },
  beforeUnmount() {
    this.stopAutoRotate();
  },
  expose: ["next", "prev"], // expose    next prev   
};
</script>

<style scoped lang="scss">
.text-move {
  transition: opacity 0.5s, transform 0.5s;
  &.next {
    transform: translateY(120px);
    opacity: 0;
    transition: opacity 0.25s, transform 0.25s;
  }
}

.fade-move-enter-active,
.fade-move-leave-active {
  transition: opacity 0.5s, transform 0.5s;
}
.fade-move-enter,
.fade-move-leave-to {
  opacity: 0;
  transform: translateY(-120px);
}

.space-nowrap {
  white-space: nowrap;
}
</style>
